import React, {Component} from 'react'
import {Nav} from 'react-bootstrap';
import NavLink from 'react-bootstrap/NavLink';
import {BrowserRouter as Router, Route, Link } from 'react-router-dom'
import ColOne from '../Elements/FirstCol';
import SecondTable from '../Elements/Secondtable/OneTable';
import ThreeFirst from '../Elements/ThreeTable/FirstTable';
import FourFirst from '../Elements/FourTable/FirstTable';
import FirstFive from '../Elements/FiveTable/FirstTable';
import FirstSix from '../Elements/SixTable/FirstTable';
import SevenSecond from '../Elements/SevenTable/SecondTable';




class NaviBar extends Component{
    render(){
        return(
            <Router>
            <div className="allinfo">
                <Nav className="">
  <Nav.Item>
    <Nav.Link className="navik">
    <div className="element">
    <Link to="/" className="LinkStyle"> 
     <p className="navtext">Первый заместитель министра - начальник криминальной милиции</p>
     </Link>              
    </div>
    </Nav.Link>
  </Nav.Item>
  <Nav.Item>
    <Nav.Link className="navik1">
    <div className="element">
    <Link to="/about/" className="LinkStyle">
    <p className="navtext">Заместитель министра  по экономической безопасности</p>
    </Link>
    </div>
    </Nav.Link >
  </Nav.Item>
  <Nav.Item>
    <Nav.Link className="navik2">
        <div className="element">
            <Link to="/users/" className="LinkStyle">
            <p className="LinkStyle">Заместитель министра – начальник милиции общественной безопасности</p>
            </Link>
    </div>
    </Nav.Link>
  </Nav.Item>
  <Nav.Item>
    <Nav.Link className="navik3">
    <div className="element">
        <Link to="/four/" className="LinkStyle">
                    <p className="LinkStyle">Заместитель министра - начальник штаба</p>
                    </Link>
    </div>
    </Nav.Link>
    </Nav.Item>
    <Nav.Item>
        <NavLink className="navik4">
        <div className="element">
            <Link className="LinkStyle" to="/five">
                    <p className="LinkStyle">Заместитель министра – начальник управления по работе с личным составом</p>
            </Link>
        </div>
        </NavLink>
    </Nav.Item>
    <Nav.Item>
        <Nav.Link className="navik5">
        <div className="element">
            <Link className="LinkStyle" to="/six">
                    <p className="LinkStyle">Заместитель министра – начальник тыла</p>
            </Link>
                </div>
        </Nav.Link>
    </Nav.Item>
    <Nav.Item>
        <Nav.Link className="navik6">
        <div className="element">
            <Link className="LinkStyle" to="/seven/">
                    <p className="LinkStyle">Заместитель министра  – начальник главного следственного управления при МВД</p>
            </Link>        
                </div>
        </Nav.Link>
    </Nav.Item>


</Nav>
<Route path="/" exact component={ColOne} />
        <Route path="/about/" component={SecondTable} />
        <Route path="/users/" component={ThreeFirst} />
        <Route path="/four/" component={FourFirst} />
        <Route path="/five" component={FirstFive} />
        <Route path="/six" component={FirstSix} />
        <Route path="/seven/" component={SevenSecond} />
            </div>
            </Router>
        )
    }
} 
export default NaviBar;