import React, {Component} from 'react'
import Second from './SecondTable';
import Threes from './ThreeTable';


class ThreeFirst extends Component {
    render(){
        return(
            <div>
            <div className="threetable">
                <div className="row linethreebboard">
                    <div className="col linethreebboardtop">
                        <div className="yellowblock3">
                        <p className="text1">Управление по обеспечению общественного порядка </p>
                        </div>
                    </div>
                    <div className="col linethreebboardtop">
                        <div className="yellowblock3">
                            <p className="text1">Управление организации деятельности участковых уполномоченных милиции и подразделений по делам несовершеннолетних</p>
                        </div>
                        </div>
                        <div className="col linethreebboardtop">
                            <div className="yellowblock3">
                                <p className="text1">Отдел организации лицензионноразрешительной работы и контроля за частной детективной и охранной деятельностью</p>
                            </div>
                        </div>
                        <div className="col linethreebboardtop">
                            <div className="yellowblock3">
                                <p className="text1">Отдел организации дознания </p>
                            </div>
                        </div>
                        <div className="col linethreebboardtop">
                            <div className="yellowblock3">
                                <p className="text1">Отдел организации деятельности специальных учреждений милиции и конвоирования </p>
                            </div>
                        </div>
                        <div className="col linethreebboardtop">
                            <div className="yellowblock3">
                                <p className="text1">Управление ГИБДД </p>
                        </div>
                        </div>
                        <div className="col linethreebboardtop">
                            <div className="yellowblock3">
                                <p className="text1">Отдел милиции на метрополитене</p>
                            </div>
                        </div>          
                        </div>
                    </div>
                    <div>
                        <Second/>
                    </div>
                    <div>
                        <Threes/>
                    </div>
                </div>
        )
    }
}
export default ThreeFirst;